
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body {
    font-family: "Segoe UI"
}
section {
    width: 100%;
    height: 100%;
    padding: 4px;
    overflow: hidden;
}
.images {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}
#main {
    padding: 5px;
    max-width: 100%;
}
.wrapper {
    max-width: calc(100% / var(--columns));
    padding: 4px;
}
.images img {
    width: 100%;
    border-radius: 4px;
    box-shadow: 0 0 1px 0 grey;
}

.title p {
    font-weight: 700;
}

.big {
    font-size: 20px;
}

#map {
    position: absolute;
    right: 0;
    top: 0;
    width: 65%;
    height: 300px;
    border-bottom-left-radius: 5px;
}

.content-inner {
    width: 100%;
    height: 100%;
}
.content {
    width: 100%;
    height: 100%;
}
.buttons {
    position: fixed;
    right: 10px;
    bottom: 10px;
    display: flex;
    column-gap: 10px;
}
#col {
    font-size: 30px;
    padding: 10px 20px;
    background-color: green;
    color: #fff;
    border: none;
    border-radius: 4px;
}
#col:hover {
    cursor: pointer;
}

.badges {
    background-color:#02D15C;
    color: #fff;
    border-radius: 4px;
    padding: 2px;
    margin-left: 5px;
}
.price p:first-child {
    font-size: 20px;
    font-weight: 600;
}
.price p {
    display: inline;
}
.price {
    margin-bottom: 5px;
}
.rating {
    display: flex;
    column-gap: 5px;
}
.rating img {
    width: 20px;
    height: 20px;
}
.stars{
    display: flex;
    align-items: center;
}
.user {
    padding: 10px 0;
}
.content-top {
    max-width: 35%;
    min-height: 280px;
}
.description {
    max-width: 40%;
}
p {
    white-space: pre-line;
}
.content-bottom {
    margin-top: 20px;
    display: flex;
    align-items: flex-start;
}
.images {
    flex-grow: 0;
}

.item-list {
    display: flex;
    flex-direction: column;
}

.item {
    border-bottom: 1px black solid;
    padding: 4px;
    display: flex;
    column-gap: 4px;
}

.item .description {
    float: right;
    max-width: 100%;
}

.item.selected {
    background-color: rgba(217, 217, 217, 0.45);
}

.container.one {
    min-width: 30%;
}

.container.img {
    width: 100%;
    flex-basis: 100%;
}

@media only screen and (max-width: 1000px) {
    #map {
        position: fixed;
        width: 100vw;
        margin-bottom: 10px;
    }
    .item-list {
        margin-top: 300px;
    }
    .price p {
        display: block;
        margin-left: 0;
    }
    #main {
        padding: 0;
    }
    .content {
        display: none;
    }
    .buttons {
        display: none;
    }
}